
.photos-container {
  position: relative;
}

.photos-container .slides .slide{
  position: relative;
}

.photos-container #next, .photos-container #previous {
  position: absolute;
  background-color: rgba(0,0,0,0.8);
  z-index: 999;
  cursor: pointer;
  font-size: 25px;
  color: #fff;
  padding: 5px 5px;
  top: 50%;
  transition: 0.5 ease-in-out;
}

#next {
  right: 20px;
  border-radius: 0 3px 3px 0;
}

#previous {
  left: 20px;
  border-radius: 3px 0 0 3px;
}

#next:hover{
  right: 15px;
}
#previous:hover {
  left: 15px;
}

.photos-container .slides .slide .slide-number{
  position: absolute;
  border-radius: 50%;
  background-color: rgba(0,0,0,0.8);
  top: 10px;
  left: 20px;
  color: #fff;
}

.photos-container .slides .slide .slide-number h4{
  margin: 0;
  padding: 1px 1px;
  font-size: 12px;
}

.photos-container .slides .slide .caption{
  font-size: 20px;
  color: #fff;
  position: absolute;
  bottom: 20px;
  right: 20px;
  left: 20px;
  background-color: rgba(0,0,0,0.8);
}


.photos-container .slides .slide .caption h3, p{
  margin: 2px 0 10px;
  padding: 5px 5px;
}


.photos-container .slides .slide img{
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center;
}

.fade {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}

@keyframes fade {
  from {opacity: .4}
  to {opacity: 1}
}


@media (max-width: 767px) {

  .photos-container .slides .slide img{
    height: 100%;
  }
  .photos-container .slides .slide .caption{
    font-size: 10px;
    bottom: 10px;
    right: 10px;
    left: 10px;
  }


  .photos-container .slides .slide .slide-number{
    top: 5px;
    left: 10px;
    font-size: 10px;
  }
  .photos-container .slides .slide .slide-number h4{
    font-size: 8px;
  }
}
